.cards{
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
}
.card{
    width: 280px;
    height: 250px;
    border-radius: 10px;
    border: 4px solid #5e0052;
    box-shadow: 0px  5px 10px rgb(0, 0, 0, 0.50);
    margin: 20px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    text-align: center;
}
.card:hover{
    border: 4px solid #831f76;
}
.btn-vm{
    background-color: #5e0052;
    padding: 10px;
    margin: 0px;
    border: 3px solid #5e0052;
    border-radius:0px 0px 10px 10px;
}
.btn-vm:hover{
    background-color:#770067;
    border: 3px solid #770067;
}
.link{
    font-size: 24px; 
}
.details{
    padding: 5px ;
}
.instituto, .titulo{
    margin: 15px;
    font-family: 'Roboto';
    font-style: normal;
    font-weight: 400;
    font-size: 20px; 
    padding-bottom: 5px;
}
h3,p{
    font-size: 22px;
    padding: 10px 0;
}
.group{
    width: 100%;
    margin: auto;
}
/*proyectos*/
.link-pro{
    color:white;
    background-color: #831f76;
    border:4px solid #831f76;
    border-radius: 10px;
}
.container{
    display: flex;
    align-items: center;
    justify-content: space-between;
    text-align: center;
    padding: 15px;
    margin: 20px;
    border:4px solid #831f76;
    border-radius: 10px;
}
@media screen and (min-width:1024px) {
    .cards{
        margin: 30px;
    }
    .details{
        margin:5px 40px ;
    }
}
